import {Select} from "./select/select";


const select = new Select('#select', {
    placeholder: 'Select item',
    selectedId: '2',
    data: [
        {id: '1', value: 'HTML'},
        {id: '2', value: 'CSS'},
        {id: '3', value: 'JS'},
        {id: '4', value: 'React'},
        {id: '5', value: 'Redux'},
        {id: '6', value: 'Vue'},
    ],
    onSelect(item) {
        console.log('Selected item', item)
    }
});

window.s = select

